.header {
  display: flex;
  width: 100vw;
  position: fixed;
  border-bottom: 5px solid orange;
  flex-wrap: wrap;
}


h1.heading-title{
  font-size: 3rem;
  letter-spacing: .2vw;
  white-space: nowrap;
  padding: 0;
}


.header-image {
  display: flex;
  z-index: -3000;
  position: absolute;
  max-height: 30vh;
  width: 100%;
  object-fit: cover;
  object-position: 80%;
}

a{
 color:white;
 text-decoration: none;
}

a *{
 display: flex;
}

.social-icon:hover{
 font-size: 2.5rem;
}


.header-content {
 display: flex;
 width: 100%;
 font-size: 1.5vw;
 justify-content: space-evenly;
}

.header-content > *{
  padding: 2vw;
}

.social-icons{
 display: flex;
}

.social-icons > *{
  padding-left: 2vw;
}


.phone-icon{
  padding-right: 1vw;
}

.menu-button{
  display: none;
}


@media only screen and (max-width: 1280px){
 .header {
   max-width: 70vw;
  }
 .logo{
  max-height: 25vh;
 }
 h1.heading-title {
  font-size: 4vw;
 }
 .media-icons{
  font-size: 1rem;
 }
 .nav{
  display: none;
 }
 .menu-button{
  display: block;
 }
} 
 
@media only screen and (max-width: 480px){
 .header{
  max-width: 75vw;
}
.logo{
  max-height: 15vh;
 }
 h1.heading-title {
  font-size: 5vw;
 }
 .media-icons{
  font-size: 1rem;
 }
 .nav{
  display: none;
 }
 .menu-button{
  display: block;
 }
}